*,
::after,
::before {
    box-sizing: border-box;
}

html {
    display: block;
    height: 100%;
}

body {
    display: block;
    height: 100%;
    padding: 80px;
    margin: 0;
}

.container {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
}
